.App {
  text-align: center;
  padding-top: 20px;
}

h1{
  font-family: 'Metal Mania', cursive;
  font-size: 52px;
  font-weight: 100;
  text-shadow: 6px 6px 8px #b8b9be,-4px -5px 2px #ffffff;
}

h2{
  font-family: 'Metal Mania', cursive;
  font-weight: 100;
}

h3{
  margin: 0px;
  font-size: 1rem;
  font-weight: 400;
  font-family: 'Abel', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
